.about {
    width: 38rem;
    margin-top: 32rem;
    color: #FFFFFF;

    &__logo {
        margin-bottom: 3rem;
    }

    &__title {
        margin-bottom: 2.4rem;
        font-weight: 700;
        font-size: 4.6rem;
        line-height: 6.4rem;
    }

    &__description {
        font-weight: 300;
        font-size: 2rem;
        line-height: 3.2rem;
        color: rgba(255,255,255,0.8);
    }
}

@media (min-width: 320px) and (max-width: 1023px) {
    .about {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 90%;
        margin: 0 auto;
        margin-top: 12rem;

        &__logo {
            display: none;
        }

        &__title {
            max-width: 52.5rem;
            margin-bottom: 2rem;
            font-size: 3rem;
            line-height: 4.2rem;
            text-align: center;
        }

        &__description {
            width: auto;
            max-width: 43.5rem;
            font-size: 1.8rem;
            line-height: 3.2rem;
            text-align: center;
        }
    }
}
